import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import MainTitle from "../Texts/MainTitle";
import "./Login.css";
import { Container } from "@mui/material";

const loginContent = {
    padding:'2em',
    width:'300px',
    backgroundColor:'white'
}

const Login = () => {
  return (
    <div className="loginSite">
      <MainTitle textContent="Login" />
      <Container>
      <Box sx={{ marginTop: "20px",display:'flex',alignItems:'center',justifyContent:'center' }}>
        <div style={loginContent}>
        <TextField label="usuario" fullWidth />
        <TextField label="contraseña" fullWidth sx={{mt:2}} />
        <Button variant="contained" fullWidth sx={{mt:2}}>
            Iniciar Sesion
        </Button>
        <Typography variant="subtitle1" sx={{mt:2,fontSize:'0.8em'}} color="text.secondary">
        amet consectetur adipisi. ccusantium emals
        </Typography>
        </div>
      </Box>
      </Container>
    </div>
  );
};

export default Login;
