import {
  Container,
  Grid,
  Card,
  Button,
  TextField,
  CardContent,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Box,
  Typography,
  IconButton,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import MainTitle from "../Texts/MainTitle";
import MainButton from "../MainButton";
import DeleteIcon from "@mui/icons-material/Delete";
import "./CartProducts.css";
import emailjs from "emailjs-com";

const CardProducts = ({
  title,
  amount,
  id,
  total,
  setDataStorage,
  dataStorage,
}) => {
  const deleteProduct = (id) => {
    const result = dataStorage.filter((dish) => dish.id !== id);
    localStorage.setItem("myOrder", JSON.stringify(result));
    setDataStorage(result);
    if (localStorage.myOrder === "[]") {
      localStorage.removeItem("myOrder");
      setDataStorage(null);
    }
  };
  return (
    <div>
      <Card sx={{ display: "flex", marginTop: "1em" }}>
        <Box sx={{ alignItems: "center", display: "center" }}>
          <div
            style={{
              marginLeft: "1em",
              padding: "5px 5px 5px 5px",
              width: "20px",
              textAlign: "center",
              background: "#DDDDD6",
            }}
          >
            {amount}
          </div>
        </Box>
        <Box
          sx={{
            flexDirection: "column",
            display: "flex",
            paddingBottom: "0",
            width: "100%",
          }}
        >
          <CardContent sx={{ flex: "1 0 auto" }}>
            <Typography component="div" variant="h5">
              {title}
            </Typography>
            <Typography
              variant="subtitle1"
              sx={{ marginTop: "-5px", fontSize: "0.9em" }}
              color="text.secondary"
              component="div"
            >
              {total} $
            </Typography>
          </CardContent>
        </Box>
        <Box sx={{ display: "flex", alignItems: "center", pl: "2%", pb: 0 }}>
          <IconButton
            onClick={() => {
              deleteProduct(id);
            }}
          >
            <DeleteIcon />
          </IconButton>
        </Box>
      </Card>
    </div>
  );
};

const CartProducts = () => {
  const getStorage = JSON.parse(localStorage.getItem("myOrder"));
  const [dataStorage, setDataStorage] = useState(null);
  const [open, setOpen] = useState(false);
  const [textEmail, setTextEmail] = useState("");
  const [textName, setTextName] = useState("");
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    if (window.localStorage.length !== 0) {
      setDataStorage(getStorage);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const TotalAmount = () => {
    if (dataStorage !== null) {
      const totalCardSum = dataStorage.reduce(
        (totalSum, currentTotal) => totalSum + currentTotal.total,
        0
      );
      return `${totalCardSum} $`;
    }
  };

  const sendReservation = () => {
    setOpen(false);
    const total = document.querySelector(".totalValue").innerText;
    const myCartImg = dataStorage.map(
      (element) =>
        `
      Nombre: ${element.title}
      Precio unitario: ${element.price}
      Cantidad: ${element.amount}
      `
    );

    const templateParams = {
      nameClient: textName,
      emailClient: textEmail,
      total: total,
      dishes: myCartImg,
    };
    emailjs
      .send(
        "service_96u7k67",
        "template_zruitvf",
        templateParams,
        "user_q5cFAR8otIbcJgpjd8fqK"
      )
      .then(
        (response) => {
          console.log("SUCCESS!", response.status, response.text);
        },
        (err) => {
          console.log("FAILED...", err);
        }
      );
  };

  return (
    <div className="cartSection">
      <Container>
        <MainTitle textContent="Carrito de compras" />
        <Grid
          container
          spacing={{ xs: 2, md: 3 }}
          columns={{ xs: 4, sm: 8, md: 12 }}
        >
          <Grid item xs={12} sm={4} md={6}>
            {dataStorage !== null ? (
              dataStorage.map((items) => {
                return (
                  <div key={items.id}>
                    <CardProducts
                      id={items.id}
                      title={items.title}
                      description={items.description}
                      amount={items.amount}
                      price={items.price}
                      total={items.total}
                      setDataStorage={setDataStorage}
                      dataStorage={dataStorage}
                    />
                  </div>
                );
              })
            ) : (
              <h1>No hay nada en el carrito</h1>
            )}
          </Grid>
          <Grid item xs={12} sm={4} md={6}>
            <div>
              {dataStorage !== null && (
                <div style={{ marginTop: "1em" }}>
                  <div className="contentItems">
                    <span className="totalText">Total:</span>
                    <span className="totalValue">{<TotalAmount />}</span>
                    <MainButton
                      buttonName="Comprar"
                      onclick={handleClickOpen}
                    />
                  </div>
                </div>
              )}
            </div>
          </Grid>
        </Grid>
        <Dialog open={open} onClose={handleClose}>
          <DialogTitle>Validar reserva</DialogTitle>
          <DialogContent>
            <DialogContentText>
              To subscribe to this website, please enter your email address
              here. We will send updates occasionally.
            </DialogContentText>
            <TextField
              autoFocus
              margin="dense"
              id="emailClient"
              label="Correo electronico"
              type="email"
              fullWidth
              required
              variant="standard"
              value={textEmail}
              onChange={(e) => setTextEmail(e.target.value)}
            />
            <TextField
              sx={{ marginTop: "1em" }}
              autoFocus
              margin="dense"
              id="nameClient"
              label="Nombre del cliente"
              type="text"
              fullWidth
              required
              variant="standard"
              value={textName}
              onChange={(e) => setTextName(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button
              onClick={() => {
                sendReservation();
              }}
            >
              Subscribe
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </div>
  );
};

export default CartProducts;
