import {
  CardContent,
  Container,
  Grid,
  Skeleton,
  Typography,
  Dialog,
  DialogContent,
  DialogTitle,
  Box,
  Card,
  CardHeader,
  Avatar,
  CardMedia,
  CardActions,
  IconButton,
  Stack,
  Button,
  Snackbar,
} from "@mui/material";
import React, { useEffect } from "react";
import MainTitle from "../Texts/MainTitle";

import PropTypes from "prop-types";

import AddCircleIcon from "@mui/icons-material/AddCircle";
import RemoveCircleIcon from "@mui/icons-material/RemoveCircle";
import CloseIcon from "@mui/icons-material/Close";

import json2mq from "json2mq";
import { useMediaQuery } from "@mui/material";

import { MenuData } from "./MenuData";
import "./ThirdSection.css";

import { useState } from "react";
import { useHistory } from "react-router";
//Custom hook

import { useLocalStorage } from "./useLocalStorage";

import Cart from "../Cart/Cart";

//Styling components

//Styles MODAL

const MyDialogTitle = (props) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle
      sx={{
        m: 0,
        p: 2,
        color: "white",
        bgcolor: "var(--navbar-background)",
        borderRadius: 0,
      }}
      {...other}
    >
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

MyDialogTitle.propTypes = {
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
};

const ControlsQuantity = (props) => {
  const [amount, setAmount] = useState(1);
  const [price, setPrice] = useState(`${props.price} COP`);
  const [open, setOpen] = useState(false);

  const [dish, setDishes] = useLocalStorage(`myOrder`, "");

  const [cart, setCart] = useState(0);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  let history = useHistory();
  const goToCart = () => {
    history.push("/thirdchallenge/carrito");
  };

  const sendToCart = () => {
    setOpen(true);
    const myStorage = localStorage;
    let elementsSaved = [];
    const getStorage = JSON.parse(localStorage.getItem("myOrder"));
    const myVer = myStorage.length === 0 ? elementsSaved : (elementsSaved = getStorage);
    const totalCard = parseInt(props.price)*amount;
    myVer.push({
      id:props.id,
      title: props.title,
      img: props.img,
      price: parseInt(props.price),
      amount: amount,
      total: totalCard,
      description: props.description,
    });

    if (myStorage.length === 0) {
      setCart(amount);
    } else {
      const myTotalAmount = getStorage.reduce(
        (totalAmount, element) => totalAmount + element.amount,
        0
      );
      setCart(myTotalAmount);
    }
    setDishes(elementsSaved);
  };

  const matches = useMediaQuery(
    json2mq({
      minWidth: 1200,
    })
  );
  const action = (
    <React.Fragment>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={() => {
          goToCart();
        }}
        sx={{ marginRight: 2 }}
      >
        <Cart cart={cart} />
      </IconButton>
    </React.Fragment>
  );

  const reduceAmount = () => {
    return (
      amount <= 1 ? 1 : setAmount(amount - 1),
      amount <= 1
        ? price
        : setPrice(`${parseInt(price) - parseInt(props.price)} COP`)
    );
  };

  const addAmount = () => {
    setAmount(amount + 1);
    setPrice(`${parseInt(price) + parseInt(props.price)} COP`);
  };

  return (
    <>
      <Grid container>
        <Grid
          item
          xs={12}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Stack direction={{ xs: "row" }}>
            <IconButton onClick={() => reduceAmount()}>
              <RemoveCircleIcon
                sx={{
                  fontSize: "2em",
                  color: "#D6D6D6",
                }}
              />
            </IconButton>
            <Typography
              sx={{
                lineHeight: "4em",
                paddingRight: "1em",
                paddingLeft: "1em",
                fontWeight: "lighter",
              }}
            >
              {amount}
            </Typography>
            <IconButton onClick={() => addAmount()}>
              <AddCircleIcon
                sx={{
                  fontSize: "2em",
                  color: "#D6D6D6",
                }}
              />
            </IconButton>
          </Stack>
        </Grid>
        <Grid
          item
          xs={12}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {matches === true ? (
            <Button
              onClick={() => {
                sendToCart();
              }}
              variant="contained"
              sx={{
                borderRadius: "0",
                width: "100%",
                height: "4em",
                fontSize: "1em",
                bgcolor: "var(--navbar-background)",
                "&:hover": {
                  bgcolor: "#7ECD28",
                },
              }}
            >
              Agregar {amount} al carrito{" "}
              <span
                style={{
                  position: "absolute",
                  right: "2em",
                  fontWeight: "lighter",
                  color: "#D6D6D6",
                }}
              >
                {price}
              </span>
            </Button>
          ) : (
            <Button
              onClick={() => {
                sendToCart();
              }}
              variant="contained"
              sx={{
                borderRadius: "0",
                width: "100%",
                height: "4em",
                fontSize: "0.6em",
                bgcolor: "var(--navbar-background)",
                "&:hover": {
                  bgcolor: "#7ECD28",
                },
              }}
            >
              Agregar {amount} al carrito{" "}
              <span
                style={{
                  position: "absolute",
                  right: "2em",
                  fontWeight: "lighter",
                  color: "#D6D6D6",
                }}
              >
                {price}
              </span>
            </Button>
          )}
        </Grid>
      </Grid>
      <Snackbar
        open={open}
        autoHideDuration={10000}
        onClose={handleClose}
        message={`Se ha agregado ${amount} ${props.title}`}
        action={action}
      />
    </>
  );
};

const MyDishes = () => {
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const [dataMenu, setDataMenu] = useState({});

  const handleOpen = (data) => {
    setOpenDialog(true);
    setDataMenu(data);
  };

  const handleClose = () => setOpenDialog(false);

  useEffect(() => {
    let timeOut = setTimeout(() => {
      setLoading(false);
    }, 4000);
    return () => {
      clearTimeout(timeOut);
    };
  }, []);

  return (
    <Grid
      container
      rowSpacing={5}
      columnSpacing={3}
      alignItems="center"
      justifyContent="center"
      sx={{
        marginBottom: "4em",
      }}
    >
      <Dialog
        sx={{
          "& .MuiDialog-paper": {
            borderRadius: 0,
          },
        }}
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <MyDialogTitle id="customized-dialog-title" onClose={handleClose}>
          {dataMenu.title}
        </MyDialogTitle>
        <DialogContent
          sx={{
            "&.MuiDialogContent-root": {
              padding: 0,
            },
          }}
        >
          <Box className="myScrollMenu">
            <Card sx={{ borderRadius: 0 }}>
              <CardMedia
                component="img"
                height="300"
                image={dataMenu.img}
                alt={dataMenu.title}
              />
              <CardContent sx={{ marginBottom: "2em" }}>
                <Typography
                  variant="h4"
                  sx={{ color: "black", fontSize: "2.5rem" }}
                >
                  {dataMenu.title}
                </Typography>
                <Typography sx={{ color: "#545454", fontSize: "0.9em" }}>
                  {dataMenu.description}
                </Typography>
              </CardContent>
              <CardActions sx={{ borderTop: "1px solid #d1d1d1" }}>
                <ControlsQuantity
                  id={dataMenu.id}
                  price={dataMenu.price}
                  title={dataMenu.title}
                  img={dataMenu.img}
                  description={dataMenu.description}
                />
              </CardActions>
            </Card>
          </Box>
        </DialogContent>
      </Dialog>
      {MenuData.map((data) => {
        return (
          <React.StrictMode key={data.id}>
            <Grid item xs={12} md={4}>
              <Card
                onClick={() => handleOpen(data)}
                sx={{
                  maxWidth: "100%",
                  background: "#1E1E1E",
                  cursor: "pointer",
                }}
              >
                <CardHeader
                  sx={{ color: "white" }}
                  avatar={
                    <Avatar
                      sx={{
                        background: "var(--primary)",
                        color: "var(--navbar-background)",
                        fontWeight: "bolder",
                      }}
                      aria-label="recipe"
                    >
                      {data.title.substring(0, 2).toUpperCase()}
                    </Avatar>
                  }
                  title={data.title}
                  subheader={
                    <Typography variant="caption" sx={{ color: "#BABABA" }}>
                      {data.date}
                    </Typography>
                  }
                />

                {loading ? (
                  <Skeleton
                    animation="wave"
                    variant="rectangular"
                    width="100%"
                    height={200}
                    sx={{ background: "#333333" }}
                  />
                ) : (
                  <CardMedia
                    component="img"
                    height="194"
                    src={data.img}
                    alt={data.title}
                  />
                )}
                <CardContent>
                  <Typography variant="body2" color="#BABABA">
                    {data.description}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </React.StrictMode>
        );
      })}
    </Grid>
  );
};

const ThirdSection = () => {
  return (
    <div className="third-section">
      <Container>
        <MainTitle
          textContent="Menu"
          description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent eget suscipit diam. Suspendisse iaculis tellus consequat neque feugiat, nec efficitur sem tempus. Morbi rhoncus erat nisl. Nunc fermentum enim nunc, a volutpat orci sollicitudin lacinia. Morbi porta dignissim est quis."
        />
        <MyDishes />
      </Container>
    </div>
  );
};

export default ThirdSection;
